<template>
  <div class="page-view">
    <navbar :title="title"></navbar>
    <filter-bar :filterData="types"></filter-bar>
    <list :courses="courses"></list>
    <search-bar></search-bar>
  </div>
</template>

<script type="text/ecmascript-6">
  import List from '../course/templates/CouseList1.vue'
  export default{
    data () {
      return {
        id:this.$route.params.id,
        title:this.$store.state.discover.title,
        perpage:10,
        page:0,
        courses:[],
        types: [
          /*{
            label: '所有课程',
            method: () => {
              this.$router.push('/searchtype')
            }
          },
          {
            label: '直播课程',
            method: () => {
              this.$router.push('/searchtype')
            }
          },
          {
            label: '录播课程',
            method: () => {
              this.$router.push('/searchtype')
            }
          }*/
        ]
      }
    },
    created(){
      var self=this;
      this.types=this.$store.state.discoverTypes.map(function (item) {
         return {
           label: item.title,
           method: () => {
             self.id=item.id
             self.title=item.title
             self.getData()
           }
         }
      })
      this.getData()
    },
    methods:{
      getData(){
        var self=this;
        this.$api.classifiedcourse({catalog_id:this.id,perpage:this.perpage,page:this.page}).then(function (res) {
           if(res.data&&res.data.code==1){
             self.courses=res.data.msg
           }
        })
      }
    },
    components: {
      List
    }
  }
</script>

<style scoped>

</style>



